* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Mulish", sans-serif !important;
}

img {
  max-width: 100%;
  height: auto;
}

.App {
  display: flex;
  position: relative;
  min-height: 100vh;
  width: 100vw;
  max-width: 1900px;
  margin: 0rem auto;
}

body {
  max-width: 100%;
  overflow-x: hidden;
}

body {
  color: rgb(255, 255, 255);
}

::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: rgba(224, 224, 224, 0.8784313725);
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
}

/* Handle on Hover */
::-webkit-scrollbar-track:hover {
  background: #555;
}

@media (max-width: 600px) {
  .App {
    height: 100vh;
    width: 100%;
    max-width: 1900px;
    margin: 3rem auto;
  }
  body {
    width: 100%;
    min-height: 100vh;
  }
}/*# sourceMappingURL=App.css.map */